@use "base/variables" as v;

.citas {
list-style: none;
padding: 0;
  li{
    padding: 1rem;
    border-top: 1px solid v.$blanco;
    p{
      font-weight: v.$bold;
      color: v.$azul;
    }
    span, .servicio{
      font-weight: v.$regular;
      color: v.$blanco;
    }
  }
}

.theader{
  font-size: 2.5rem;
}

.service{
  @extend .citas;

  .actions{
    margin-top: 2rem;
    justify-content: left;
    .boton, .boton-eliminar{
      margin: 0 3rem 1rem 0;
    }
  }
}