@use "base/variables" as v;
@use "base/mixins" as m;

.actions{
  margin-top: v.$separacion;
  @include m.tablet{
     display: flex;
     justify-content: space-between;
    align-items: center;
  }
  a{
    color: v.$blanco;
    display: block;
    margin-bottom: calc(v.$separacion / 1);
    text-align: center;
    text-decoration: none;
  }
}