@use "base/variables" as v;

.alert{
  padding: calc(v.$separacion / 4);
  color: v.$blanco;
  width: 100%;
  display: block;
  margin-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: v.$bold;
  text-transform: uppercase;
  &.error{
    background-color: v.$rojo;
  }
  &.succes{
    background-color: v.$verde;
  }
}