@use "base/variables" as v;
@use "base/mixins" as m;

.list-services{
  display: grid;
  gap: 2rem;
  @include m.tablet{
    @include m.grid(2, 2rem)
  }
  .service{
    background-color: v.$blanco;
    padding: 3rem;
    border-radius: 1rem;
    transition-property: transform;
    transition-duration: 300ms;
    text-align: center;
    cursor: pointer;

    &.selected{
      background-color: v.$azul;
      p{
        color: v.$blanco;
      }
    }

    &:hover{
      transform: scale(1.05);
    }

    p{
      color: v.$negro;
      margin: 0 0 calc(v.$separacion / 4) 0;
    }

    .price-service{
      color: v.$azul;
      font-weight: v.$bold;
      font-size: 3rem;
      margin: 0;
    }
  }
}