@use "base/mixins" as m;
@use "base/variables" as v;

.container-app {
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }

  .image {
    height: 20rem;
    background-image: url("../img/1.jpg");
    background-size: cover;
    background-position: center center;

    @include m.tablet {
      height: auto;
    }
  }

  .app {
    width: 100%;
    margin: 0 auto;
    padding: 3rem;
    @include m.tablet{
      overflow: scroll;
      overflow-x: auto;
      overflow-y: auto;
    }
  }

  .barra {
    margin: v.$separacion 0;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .boton {
      @include m.boton(v.$negro, v.$blanco);
      margin: 0rem;
    }
  }
}

.barra-servicio{
  margin-bottom: v.$separacion;
  @include m.tablet{
    display: flex;
    justify-content: space-between;
  }

  .boton{
    margin: 0 0 1rem 0;
  }
}