
@use "base/variables" as v;
@use "base/mixins" as m;

.tabs{
  button{
    background-color: v.$azul;
    border: none;
    color: v.$blanco;
    padding: 1rem;
    display: block;
    width: 100%;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-weight: v.$bold;
    transition: background-color 300ms;

    &:hover{
      cursor: pointer;
      background-color: darken(v.$azul, 10%);
    }

    &.actual{
      background-color: v.$blanco;
      color: v.$azul;
    }
  }

  @include m.tablet{
    display: flex;
  }
}