@use "base/variables" as v;
@use "base/mixins" as m;

.pagination{
    padding: calc(v.$separacion / 5) 0 0 0;
    margin-top: -2rem;

    @include m.telefono{
        display: flex;
        justify-content: space-between;
    }

    button{
      margin-bottom: 0 0 1rem 0;
    }
}

.hidden{
  visibility: hidden;
}